<template>
    <div class="app-container course">
        <input ref="file" type="file" accept="image/*" style="opacity: 0; position: fixed; top: -1000px;" @change="upload">
        <el-button v-if="!isUpload" type="primary" @click="emitFile" :loading="uploading">上传图片</el-button>
        <div v-if="isUpload">
            <el-button type="primary" @click="submit">确定</el-button>
            <el-button type="primary" @click="cancel">取消</el-button>
        </div>
        <div class="course-img-wrap" v-loading.body="uploading">
            <img v-if="prevImg" :src="prevImg">
        </div>
    </div>
</template>

<script>
import { upload } from "@/api/member";
import { edit, info } from "@/api/course";
import { imgBeforeUpload } from "@/utils";

export default {
  data() {
    return {
      courseImg: "",
      prevImg: "",
      pic_id: "",
      isUpload: false,
      uploading: false
    };
  },
  created() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      info().then(response => {
        if (response.data.length) {
          this.courseImg = "http://" + response.data;
          this.prevImg = "http://" + response.data;
        }
      });
    },
    upload(e) {
      // 参考：http://www.cnblogs.com/cjh1111/p/7017295.html
      let file = e.target.files[0];
      if (!file) {
        return;
      }
      this.uploading = true;      
      imgBeforeUpload(file)
        .then(rst => {
          upload(rst.formData).then(response => {
            this.prevImg = "http://" + response.data.pic_address;
            this.pic_id = response.data.pic_id;
            this.isUpload = true;
            this.uploading = false;
          });
        })
        .catch(err => {
          this.$message.error(err.toString());
          this.uploading = false;
        });
    },
    emitFile() {
      this.$refs.file.click();
    },
    submit() {
      edit({ pic_id: this.pic_id }).then(response => {
        this.courseImg = this.prevImg;
        this.isUpload = false;
      });
    },
    cancel() {
      this.prevImg = this.courseImg;
      this.isUpload = false;
    }
  }
};
</script>

<style lang="scss">
.course {
  .course-img-wrap {
    margin-top: 15px;
    width: 100%;
    text-align: center;
    img {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      width: 100%;
      max-width: 960px;
    }
  }
}
</style>




