import fetch from '@/utils/fetch1';
const qs = require('qs');

export function edit(params) {
    return fetch({
        url: '/api/course/edit',
        method: 'post',
        data: qs.stringify(params),
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
}

export function info() {
    return fetch({
        url: '/api/course/info',
        method: 'get'
    });
}



